@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Components > Confirm
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */
.jconfirm {
    &.danger.danger {
        .jconfirm-box.jconfirm-box {
            // border: 2px solid lighten($danger, 20%);

            &.hilight.hilight {
                box-shadow: 0 0 12px 2px rgba($danger, 0.44);
            }

            .title.title {
                border-bottom-color: $danger;
            }
        }
    }

    &.warning.warning {
        .jconfirm-box.jconfirm-box {
            // border: 2px solid lighten($secondary, 20%);

            &.hilight.hilight {
                box-shadow: 0 0 12px 2px rgba($warning, 0.44);
            }

            .title.title {
                border-bottom-color: $warning;
            }
        }
    }

    &.info.info {
        .jconfirm-box.jconfirm-box {
            // border: 2px solid lighten($info, 20%);

            &.hilight.hilight {
                box-shadow: 0 0 12px 2px rgba($info, 0.44);
            }

            .title.title {
                border-bottom-color: $info;
            }
        }
    }

    .jconfirm-box-container {
        .jconfirm-box.jconfirm-box {
            @include border-radius(8px);

            &.hilight {
                &.hilight {
                    box-shadow: 0 0 12px 2px rgba($dark, 0.44);
                }
            }

            .title.title {
                border-bottom: 2px solid $light;
                font-size: 1.25rem;
                font-weight: bold;
                padding: 1rem 1.5rem .75rem 1.5rem;

                > i {
                    font-size: 1.75rem;
                    vertical-align: middle;
                }

                > div {
                    display: inline-block;
                    vertical-align: middle;
                    padding-left: .5rem;
                }
            }

            .content.content {
                color: $grey;
                line-height: 1.75;
                padding: 1rem 1.5rem;
                word-wrap:break-word;
            }
        }
    }
}