@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Layout > Breadcrumbs
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

.mask-icons {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("../../images/bg-icon_1.png") center center;
    opacity: .52;
}

.sec-breadcrumbs {
    position: relative;
    background-color: darken($w-primary, 0);
    line-height: 1.875;

    &.inner {
        padding: 3.5rem .75rem 3.25rem;

        @media (max-width: $screen-sm-min) {
            padding: 2.5rem .5rem rem;
        }
    }

    &:not(.inner) {
        padding: 2.25rem .75rem;

        @media (max-width: $screen-sm-min) {
            padding: 2rem 0.5rem;
        }
    }

    h2 {
        position: relative;
        color: $white;
        font-size: 1.75rem;
        font-weight: 500;
        letter-spacing: 3px;
        margin-bottom: .75rem;
        padding: 0 0 1.75rem 0;
        text-shadow: 0 0 8px rgba($dark , .24);
        z-index: 1;

        @media (max-width: $screen-sm-min) {
            // padding-left: .5rem;
        }

        &:before {
            content: '';
            background-image: url("data:image/svg+xml;base64,");
            background-repeat: no-repeat;
            background-position: left bottom;
            position: absolute;
            left: 0;
            bottom: 0;
            width: pxTorem(48px);
            height: pxTorem(14px);

            @media (max-width: $screen-sm-min) {
                // left: 1.75rem;
            }
        }
    }

    ul {
        font-size: .9375rem;
        padding: 0 2rem;

        @media (max-width: $screen-sm-min) {
            padding: 0 .75rem;
        }

        i {
            color: rgba($white, 0.4);
            margin-right: .25rem;
        }

        li {
            color: rgba($white, 0.7);
            display: inline-block;
            position: relative;
            margin-right: 1.5rem;

            &:not(:last-child):after {
                content: '\f054';
                font-family: "Font Awesome 5 Free";
                -webkit-font-smoothing: antialiased;
                display: inline-block;
                font-style: normal;
                font-variant: normal;
                text-rendering: auto;
                font-size: .75rem;
                font-weight: 900;
                line-height: 1;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: -1.25rem;
                color: rgba($white, 0.4);
            }

            a {
                color: $white;

                &:hover {
                    color: rgba($white, 0.7);
                }
            }
        }
    }
}
