/*  Wel_banner
----------------------------------------  */
.Wel_banner {
    position: relative;
    background: url("../../images/wel_n_bg.jpg") center center no-repeat;
    background-size: cover;

    &:before {
        content: '';
        background: url("../../images/wave.svg") center bottom no-repeat;
        height: 3.375rem;
        background-size: 100%;
        width: 100%;
        position: absolute;
        left: 0;
        bottom: -2px;
        z-index: 2;
    }

    &:not(.pag-breadcrumbs) {
        h2 {
            @media (max-width: $screen-xs-min) {
                margin: 3rem 0 0 0;
            }
        }
    }

    h2 {
        color: $white;
        font-size: 1.875rem;
        letter-spacing: 1px;
        text-shadow: 0 2px 4px rgba(#000, 0.24);
        position: absolute;
        top: 46%;
        transform: translateY(-50%);
        z-index: 1;

        @media (max-width: $screen-sm-min) {
            position: relative;
            text-align: center;
            top: auto;
            transform: translate(0);
            margin: 4rem 0 0;
        }

        @media (max-width: $screen-xs-min) {
            font-size: 1.55rem;
        }
    }

    .mask_wel-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url("../../images/wel-bg-main.svg") center center no-repeat;
        background-size: cover;
    }

    .Wel_banner-shape {
        position: absolute;
        bottom: -1px;
        left: 0;
        width: 100%;
        height: 32px;
        background: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxMzY2IiBoZWlnaHQ9IjMyIiB2aWV3Qm94PSIwIDAgMTM2NiAzMiI+PHBhdGggZD0iTTAsMFEzNDEuNSwzMS42OCw2ODMsMzEuNjhUMTM2NiwwVjMySDBaIiBmaWxsPSIjZmZmIi8+PC9zdmc+") center center no-repeat;
        background-size: cover;
    }

    .doctors-img {
        position: relative;
        text-align: center;
        padding-top: 2.5rem;
        z-index: 1;

        @media (max-width: $screen-sm-min) {
            padding-left: 8rem;
            padding-right: 8rem;
        }

        @media (max-width: $screen-xs-min) {
            padding-left: 4rem;
            padding-right: 4rem;
        }

        img {
            height: auto;
            max-width: 100%;
        }
    }

    .animated-icon {
        position: absolute;
        user-select: none;
        opacity: .36;

        &:nth-child(3) {
            left: 46%;
            bottom: 15%;
        }

        &:nth-child(4) {
            right: 10%;
            top: 12%;
        }

        &:nth-child(5) {
            left: 5%;
            top: 14%;
        }
    }

    .rotate-me {
        animation-name: rotateme;
        animation-duration: 24s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
    }

    @keyframes rotateme {
        from {
            transform: rotate(0deg);
        }

        to {
            transform: rotate(360deg);
        }
    }
}

/*  Wel tabs
----------------------------------------  */
.Wel_apply {
    padding: 2.25rem 0 4.5rem;

    .apply-tab {
        text-align: center;

        .apply-tab-nav {
            position: relative;
            left: 0;
            top: 0;
            display: inline-block;
            vertical-align: top;
            width: 48%;
            padding: 1.25rem 1.5rem;
            border-radius: 8px;
            cursor: pointer;
            margin-top: 6rem;
            text-align: left;
            overflow: visible;
            transition: $transition-base;

            @media (max-width: $screen-xs-min) {
                margin-top: 5rem;
            }

            // @media (max-width: $screen-md-min) {
            //     width: 18rem;
            // }

            // @media (max-width: $screen-sm-min) {
            //     width: 15rem;
            //     border-radius: 3rem;
            // }

            @media (max-width: $screen-xs-min) {
                padding: 1.5rem 1rem;
            }

            .tab-txt {
                text-align: left;
                padding-left: 1.5rem;

                @include in-bk-mid;

                @media (max-width: $screen-sm-min) {
                    padding-left: .5rem;
                }

                @media (max-width: $screen-xs-min) {
                    display: block;
                    padding-left: 0;
                    text-align: center;
                    padding-top: 1rem;
                }

                h6 {
                    font-size: 2rem;
                    letter-spacing: 2px;

                    @media (max-width: $screen-sm-min) {
                        font-size: 1.5rem;
                    }

                    // @media (max-width: $screen-xs-min) {
                    //     font-size: 1.25rem;
                    // }
               
                }

                p {
                    font-size: 1.25rem;

                    @media (max-width: $screen-sm-min) {
                        font-size: 1rem;
                    }

                    // @media (max-width: $screen-xs-min) {
                    //     font-size: .875rem;
                    // }
               
                }
            }

            .tab-icon {
                position: relative;
                border-radius: 100%;
                width: 4.5rem;
                height: 4.5rem;
                margin: auto;

                @include in-bk-mid;

                @media (max-width: $screen-sm-min) {
                    width: 3.5rem;
                    height: 3.5rem;
                }

                @media (max-width: $screen-xs-min) {
                    display: block;
                }

                &:before {
                    content: '';
                    width: 5.25rem;
                    height: 5.25rem;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    border-radius: 100%;

                    @media (max-width: $screen-sm-min) {
                        width: 4rem;
                        height: 4rem;
                    }

                    @media (max-width: $screen-xs-min) {}
                }

                i {
                    font-size: 3rem;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);

                    // @media (max-width: $screen-xs-min) {
                    // font-size: 2rem;
                    // }
               
                }
            }
        }

        > input {
            + label {
                background-color: $white;
                border-width: 2px;
                border-style: solid;

                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    width: pxTorem(176px);
                    height: 100%;
                    transition: $transition-base;
                    z-index: -1;
                }

                &:after {
                    font-family: 'welfare' !important;
                    speak: never;
                    font-style: normal;
                    font-weight: normal;
                    font-variant: normal;
                    text-transform: none;
                    line-height: 1;
                    position: absolute;
                    right: -1.25rem;
                    bottom: -1rem;
                    font-size: 8.5rem;

                    @media (max-width: $screen-md-min) {
                        font-size: 6.5rem;
                    }
                }

                .tab-icon {
                    background-color: lighten($primary, 20%);

                    &:before {
                        border-width: 2px;
                        border-style: dashed;

                        @media (max-width: $screen-sm-min) {
                            border-width: 1px;
                        }
                    }
                }

                &:hover {
                    box-shadow: none;
                    top: 4px;
                    left: 4px;

                    &:before {
                        top: -4rem;
                        opacity: 0.66;

                        // @media (max-width: $screen-xs-min) {
                        //     top: -4.5rem;
                        // }
                   
                    }

                    &:after {
                        right: -1rem;
                        bottom: -.75rem;
                    }

                    .tab-icon {
                        background-color: lighten($primary, 35%);

                        &:before {
                            border: 2px dashed lighten($primary, 35%);
                        }
                    }

                    .welfare-health_Personnel_h {
                        span {
                            &:nth-child(1):before {
                                color: $dark;
                            }

                            &:nth-child(2):before {
                                color: $primary;
                            }
                        }
                    }

                    p {
                        color: lighten($primary, 35%);
                    }

                    h6 {
                        color: lighten($primary, 35%);
                    }
                }
            }

            &:checked + label {
                border-width: 2px;
                border-style: solid;
                color: $white;

                &:hover {
                    box-shadow: none;
                }

                &:before {
                    top: -5rem;

                    @media (max-width: $screen-xs-min) {
                        top: -4.25rem;
                    }
                }

                &:after {
                    opacity: 0.24;
                }

                .tab-icon {
                    background-color: $white;

                    &:before {
                        border: 2px dashed rgba($white, 0.66);
                    }

                    .welfare-health_Personnel_h {
                        span {
                            &:nth-child(1):before {
                                color: $dark;
                            }

                            &:nth-child(2):before {
                                color: $primary;
                            }
                        }
                    }
                }

                p {
                    color: $white;
                }

                h6 {
                    color: $white;
                }
            }

            &:first-of-type {
                + label {    

                    border-color: lighten($primary, 20%);
                    box-shadow: 4px 4px 0 lighten($primary, 25%);
                    color: $primary;
                    @include remCalc(margin-right,16px);

                    @media (max-width: $screen-xs-min) {
                        margin-right: .5rem;
                    }

                    &:before {
                        left: 0;
                        background: url("../../images/img-lt-dcs.svg") no-repeat center top;

                        @media (max-width: $screen-xs-min) {
                            width: 9rem;
                            background-size: 9rem;
                        }
                    }

                    &:after {
                        content: "\e906";
                        opacity: 0.12;
                    }

                    .tab-icon {
                        background-color: lighten($primary, 20%);

                        &:before {
                            border-color: lighten($primary, 20%);
                        }

                        .welfare-health_Personnel_h {
                            span {
                                &:before {
                                    color: $white;
                                }
                            }
                        }
                    }

                    &:hover {
                        background-color: lighten($primary, 15%);
                        border-color: lighten($primary, 20%);
                        box-shadow: none;

                        &:after {
                            right: -1rem;
                            bottom: -.75rem;
                        }

                        .tab-icon {
                            background-color: lighten($primary, 35%);

                            &:before {
                                border: 2px dashed lighten($primary, 35%);
                            }
                        }

                        .welfare-health_Personnel_h {
                            span {
                                &:nth-child(1):before {
                                    color: $dark;
                                }

                                &:nth-child(2):before {
                                    color: $primary;
                                }
                            }
                        }
                    }
                }

                &:checked + label {
                    background-color: $primary;
                    border-color: $primary;
                    box-shadow: 4px 4px 0 lighten($primary, 25%);

                    &:after {
                        opacity: 0.24;
                    }

                    &:hover {
                        box-shadow: none;
                    }

                    .tab-icon {
                        background-color: $white;
                    }

                    .welfare-health_Personnel_h {
                        span {
                            &:nth-child(1):before {
                                color: $dark;
                            }

                            &:nth-child(2):before {
                                color: $primary;
                            }
                        }
                    }
                }
            }

            &:last-of-type {
                + label {
                    border-color: lighten($secondary, 20%);
                    box-shadow: 4px 4px 0 lighten($secondary, 25%);
                    color: $secondary;

                    &:before {
                        right: 0;
                        background: url("../../images/img-rt-hos.svg") no-repeat center top;

                        @media (max-width: $screen-xs-min) {
                            width: 9rem;
                            background-size: 9rem;
                        }
                    }

                    &:after {
                        content: "\e907";
                        opacity: 0.08;

                        @media (max-width: $screen-md-min) {
                            font-size: 6.5rem;
                        }
                    }

                    .tab-icon {
                        background-color: lighten($secondary, 20%);

                        &:before {
                            border-color: lighten($secondary, 20%);
                        }

                        .welfare-health_Institute_h {
                            span {
                                &:before {
                                    color: $white;
                                }
                            }
                        }
                    }

                    &:hover {
                        background-color: lighten($secondary, 10%);
                        border-color: lighten($secondary, 15%);
                        box-shadow: none;

                        &:after {
                            opacity: 0.10;
                        }

                        .tab-icon {
                            background-color: lighten($secondary, 35%);

                            &:before {
                                border: 2px dashed lighten($secondary, 35%);
                            }
                        }

                        .welfare-health_Institute_h {
                            span {
                                &:nth-child(1):before {
                                    color: $dark;
                                }

                                &:nth-child(2):before {
                                    color: $secondary;
                                }
                            }
                        }
                    }
                }

                &:checked + label {
                    background-color: $secondary;
                    border-color: 2px solid $secondary;
                    box-shadow: 4px 4px 0 lighten($secondary, 25%);

                    &:after {
                        opacity: 0.12;
                    }

                    &:hover {
                        box-shadow: none;
                    }

                    .tab-icon {
                        background-color: $white;
                    }

                    .welfare-health_Institute_h {
                        span {
                            &:nth-child(1):before {
                                color: $dark;
                            }

                            &:nth-child(2):before {
                                color: $secondary;
                            }
                        }
                    }
                }
            }
        }

        .apply-tabs-content {
            position: relative;
            display: none;
            animation: fadeIn .75s ease-in-out;
            border-top: 1px solid #dee2e6;

            &:before {
                content: '';
                position: absolute;
                top: -2px;
                width: 0;
                height: 4px;
                border-radius: 4px;
                transition: $transition-base;
            }

            .apply-list {
                font-size: 0;
                text-align: left;

                li {
                    display: inline-block;
                    vertical-align: middle;
                    width: 25%;

                    @media only screen and (max-width: $screen-md-min) {
                        width: 33.33333%;
                    }

                    @media only screen and (max-width: $screen-sm-min) {
                        width: 50%;
                    }

                    a {
                        position: relative;
                        display: block;
                        color: $dark;
                        border-radius: 4px;
                        font-size: 1.125rem;
                        margin: 0 1rem 1rem;
                        padding: 1rem 0 1rem 3.25rem;

                        &:hover {
                            color: darken($dark, 5%);
                            transform: translate(2px, 2px);
                        }

                        &:before {
                            content: '';
                            position: absolute;
                            left: 1.5rem;
                            top: 50%;
                            transform: translateY(-50%);
                            border-radius: 50%;
                            width: 0.75rem;
                            height: 0.75rem;
                        }
                    }
                }
            }
        }
    }
}

/*  Tab Checked 
----------------------------------------  */
#apply1:checked ~ #apply1-content,
#apply2:checked ~ #apply2-content {
    display: block;
    margin-top: 1.5rem;
    padding-top: 1.875rem;
}

#apply1:checked ~ #apply1-content:before,
#apply2:checked ~ #apply2-content:before {
    width: 50%;
}

#apply1-content {
    &:before {
        background-color: lighten($primary, 5%);
        left: 0;
    }

    .apply-list li a {
        background-color: lighten($primary, 30%);
        box-shadow: 4px 4px 0 lighten($primary, 20%);

        &:hover {
            background-color: lighten($primary, 25%);
            box-shadow: 2px 2px 0 lighten($primary, 20%);

            &:before {
                background-color: lighten($primary, 5%);
            }
        }

        &:before {
            background-color: lighten($primary, 15%);
        }
    }
}

#apply2-content {
    &:before {
        background-color: lighten($secondary, 5%);
        right: 0;
    }

    .apply-list li a {
        background-color: lighten($secondary, 35%);
        box-shadow: 4px 4px 0 lighten($secondary, 25%);

        &:hover {
            background-color: lighten($secondary, 30%);
            box-shadow: 2px 2px 0 lighten($secondary, 25%);

            &:before {
                background-color: lighten($secondary, 5%);
            }
        }

        &:before {
            background-color: lighten($secondary, 15%);
        }
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

/*  news section
----------------------------------------  */
.news-section {
    background: url("../../images/wel_n_sec-bg.jpg") center center no-repeat;
    background-size: cover;
    padding: 3rem 0.625rem;

    .index-note {
        margin-top: pxTorem(30px);
    }

    .news-group {
        border-radius: 4px;
        border-bottom: 3px solid $w-space;
        box-shadow: 0 2px 8px 0 rgba($white, 0.16);
        margin: 0 .5rem 1rem 0;
        padding: 0;
        transition: $transition-base;

        &:hover {
            box-shadow: 0 4px 16px 0 rgba($white, 0.2);
            transform: translateY(-2px);
        }

        .date {
            background-color: $w-space;
            border-radius: 4px 4px 0 0;
            border-bottom: 1px solid lighten($primary, 5%);
            color: $white;
            font-size: .875rem;
            padding: 0.75rem 1.25rem 0.625rem;

            i {
                color: lighten($w-primary, 10%);
                margin-right: 1rem;
                font-size: 1.25rem;
                vertical-align: middle;

                &:after {
                    content: "|";
                    left: .625rem;
                    position: relative;
                    color: rgba($white, 0.66);
                }
            }
        }

        .cnt {
            background-color: $white;
            padding: .5rem 1.5rem 1rem 1.5rem;

            span {
                font-size: 0.8125rem;
                color: $w-primary;
            }

            a {
                font-size: 0.875rem;
                color: #000F2D;
                font-weight: normal;
                margin: 0;
                display: block;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
                text-overflow: ellipsis;
                line-height: 1.5rem;
                height: 3rem;
            }
        }
    }

    .btn-more {
        text-align: center;

        a {
            background-color: $white;
            color: $w-space;
            font-size: .9375rem;
            font-weight: normal;
            padding: .75rem 1.125rem;
            width: 14rem;

            &:hover {
                box-shadow: 0 2px 8px rgba($dark, 0.12);
                color: $w-primary;
                text-decoration: none;
            }
        }
    }
}

/*  banner advs
----------------------------------------  */
.banner-zone {
    background-color: $white;
    padding: 3rem 0.625rem 2.25rem;

    // @media only screen and (max-width: screen-xs-min) {
    //     padding-left: 2rem;
    //     padding-right: 2rem;
    // }

    .banner-list {
        margin: 0;
        overflow: initial;

        &.banner-list-fixed {}

        li {
            margin-right: 0;
            padding: 6px;
            border: 1px solid rgba($w-black, 0.16);
            border-radius: 6px;
            transition: $transition-base;

            &:hover {
                transform: translateY(-2px);
                box-shadow: 0 0px 1px rgba(0, 0, 0, 0.2);
            }

            a {
                display: block;
                border-radius: 4px;
            }

            img {
                width: 100%;
                border-radius: 4px;
                object-fit: contain;
                font-family: 'object-fit: contain;';
                height: 5rem;
            }
        }
    }
}

/*  #index_news & index_links
----------------------------------------  */
#index_news.owl-theme .owl-nav [class*='owl-'],
#index_links.owl-theme .owl-nav [class*='owl-'] {
    color: $white;
    line-height: 0;
    margin: 0;
    background: rgba($w-black, 0.6);
    display: inline-block;
    cursor: pointer;
    border-radius: 100%;
    width: 2.25rem;
    height: 2.25rem;
    transition: 0.25s ease-in-out;
}

#index_news.owl-theme .owl-nav [class*='owl-']:hover,
#index_links.owl-theme .owl-nav [class*='owl-']:hover {
    background: rgba($w-black, 0.88);
    color: $white;
    text-decoration: none;
}

#index_news.owl-theme .owl-nav span,
#index_links.owl-theme .owl-nav span {
    font-size: 1.5rem;
}

#index_news .owl-prev,
#index_links .owl-prev {
    position: absolute;
    left: -1.5rem;
    top: 50%;
    transform: translateY(-50%);
}

#index_news .owl-next,
#index_links .owl-next {
    position: absolute;
    right: -1.5rem;
    top: 50%;
    transform: translateY(-50%);
}

#index_news.owl-carousel .owl-stage-outer,
#index_links_fixed.owl-carousel .owl-stage-outer,
#index_links.owl-carousel .owl-stage-outer {
    padding-top: .5rem;
    padding-bottom: .5rem;
}