@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Pages > News
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

.news-list-section {
    background-color: $default;
    padding: 3rem 0 ;

    .wel-news-list {
        ul {
            .wel-news-item {
                background-color: $white;
                border-left: 3px solid lighten($w-primary, 0);
                    border-bottom: 2px solid #c9c9cb;
                border-radius: 4px;
                box-shadow: $shd--hv--ver--05;
                margin-bottom: .75rem;
                transition: $transition-base;
                position: relative;
                overflow: hidden;

                a {
                    display: block;
                    padding:.5rem 6rem 1.25rem 1.25rem;
                    user-select: none;

                    i {
                        color: rgba($w-primary, 0.24);
                        margin-right: .5rem;
                        font-size: 4rem;
                        position: absolute;
                        right: -.375rem;
                        bottom: 0.125rem;
                        transition: $transition-base;
                    }

                    .date-bk {
                        position: relative;
                        display: inline-block;
                        border-bottom: 1px dashed $border-color_7;
                        padding-bottom: 0.25rem;
                        padding-right: .75rem;

                        &:before {
                            content: '';
                            position: absolute;
                            left: 0;
                            bottom: -2px;
                            width: 3rem;
                            height: 3px;
                            border-radius: 3px;
                            background-color: lighten($w-primary, 20%);
                        }

                        .year-cnt .year {
                            color: $lightgrey;
                            font-size: .75rem;
                            font-weight: 300;
                        }

                        .date-cnt .date {
                            color: $dark;
                            font-weight: 700;
                            font-size: 1rem;
                        }

                        .date-cnt .day {
                            color: $darkgrey;
                            font-weight: 100;
                            font-size: 0.875rem;
                        }
                    }.sub-txt {
                        color:$w-primary;
                            font-size:.875rem;
                            margin-top: .75rem;
                            }

                    .txt {
                        color: $dark;
                        font-size: 1rem;
                        margin-top: .5rem;
                    }

                    &:hover {
                       
                        text-decoration: none;

                        i {
                            color: rgba($w-primary, 0.66);
                            font-size: 6rem;
                            right: -.25rem;
                              bottom: .25rem;
                        }

                        .date-bk {
                            .year-cnt .year {}

                            .date-cnt .date {}

                            .date-cnt .day {}
                        }


                        .txt {
                            
                            color: $w-dark;
                        }
                    }
                }

                &:hover {
                    box-shadow: $shd--hv--ver--05--hv;
                    // background-color: lighten($w-primary, 37%);
                    border-left: 5px solid $w-primary;
                }
            }
        }
    }
}

.news-page-title {
    h1 {
        position: relative;
        color: $dark;
        font-size: 1.75rem;
        margin: 0.625rem 0 1.25rem;
        padding: 0.375rem 1rem 0.375rem 3rem;
        letter-spacing: 1px;

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 1rem;
            width: .75rem;
            height: .75rem;
            transform: translateY(-50%) rotate(45deg);
            border-radius: 4px;
            background: rgba($w_blue, 0.36);
            background-color: lighten($w-primary, 20%);
        }
    }
    .sub-tags {
        background-color: $w-primary;
        color: $white;
        display: inline-block;
        padding: .25rem 1rem;
        border-radius: 8px;
        font-size: .9375rem;
        margin-left: 1rem;
    }

    p {
        color: $darkgrey;
        font-size: 0.875rem;
        margin: .75rem 0 0;
        border-top: 1px solid $light;
        border-bottom: 1px solid $light;
        padding: 1rem;

        > i {
            color: $w-primary;
            margin-right: .5rem;
        }
    }
}

.news-page-content {
    color: $grey;
    padding: 1.5rem 1rem;
    line-height: 1.875;

    p {
        margin-bottom: .75rem;
    }
}

.news-page-file-content {
    margin: 0 0 2rem 0;
    padding: 0 1.25rem;

    h3 {
        position: relative;
        font-size: 1.125rem;
        padding: .5rem 1rem .5rem 3rem;

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 0;
            width: 2rem;
            border-color: lighten($secondary, 30%);
            border-top-width: 2px;
            border-top-style: solid;
        }

        &:after {
            content: '';
            position: absolute;
            top: 50%;
            right: 0;
            width: calc(100% - 12rem);
            border-color: $grey-200;
            border-top-width: 1px;
            border-top-style: dashed;
        }

        i {
            color: $secondary;
            margin-right: .5rem;
        }
    }

    ul {
        margin-top: 1rem;

        li a {
            position: relative;
            display: inline-block;
            background-color: lighten($secondary, 40%);
            border-radius: 1.5rem;
            color: $dark;
            font-size: .875rem;
            margin-bottom: 0.75rem;
            padding: 0.5rem 1.75rem .5rem 2.5rem;

            &:before {
                content: '';
                background-color: lighten($secondary, 30%);
                width: 8px;
                height: 8px;
                border-radius: 100%;
                position: absolute;
                top: 50%;
                left: 1rem;
                transform: translateY(-50%);
            }
        }
    }
}

/*  Pagination
----------------------------------------  */
.wel-pagination {
    background-color: $white;
    border-top: 1px solid $border-color_7;
    border-bottom: 1px solid $border-color_7;
    margin-top: 2rem;
    padding: 1rem 2rem;
    text-align: center;

    ul {
        font-size: 0;
        text-align: center;
        width: 100%;

        li {
            display: inline-block;

            &:first-child {
                float: left;
            }

            &:last-child {
                float: right;
            }

            span {
                vertical-align: middle;
                font-size: .875rem;
            }

            a {
                display: block;
                color: $darkgrey;
                font-size: 1rem;
                width: 100%;
                height: 100%;
                padding: .5rem;

                &:hover {
                    color: $w-primary;
                }

                &.active {
                    color: $dark;
                }
            }
        }
    }

    .arrow {
        position: relative;
        display: inline-block;
        background-color: $darkgrey;
        width: 2rem;
        height: 2px;
        transition: all .125s ease-in-out;
        vertical-align: middle;

        &.lt-long-arrow:before,
        &.rt-long-arrow:before {
            position: absolute;
            top: -4px;
            content: '';
            border-width: 5px;
            border-style: dashed;
            border-color: transparent;
        }

        &.lt-long-arrow:before {
            left: -6px;
            border-right-style: solid;
            border-right-color: $darkgrey;
        }

        &.rt-long-arrow:before {
            right: -6px;
            border-left-style: solid;
            border-left-color: $darkgrey;
        }
    }
}

.arrow-icon:hover {
    text-decoration: none;

    .arrow {
        background-color: $w-primary;

        &.lt-long-arrow:before {
            border-right-color: $w-primary;
        }

        &.rt-long-arrow:before {
            border-left-color: $w-primary;
        }
    }
}