@function strip-unit($num) {
    @return $num / ($num * 0 + 1);
}

@mixin remCalc($property, $values...) {
    $max: length($values);
    $pxValues: "";
    $remValues: "";
    $base: strip-unit(16px);

    @for $i from 1 through $max {
        $value: strip-unit(nth($values, $i));
        $pxValues: #{$pxValues + $value}px;

        @if $i < $max {
            $pxValues: #{$pxValues + " "};
        }
    }

    @for $i from 1 through $max {
        $value: strip-unit(nth($values, $i));
        $remValues: #{$remValues + $value / $base}rem;

        @if $i < $max {
            $remValues: #{$remValues + " "};
        }
    }

    #{$property}: $pxValues;
    #{$property}: $remValues;
}