@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Layout > Navbar
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */
@keyframes slide-down {
    0% {
        transform: translateY(-100%);
    }

    100% {
        transform: translateY(0);
    }
}

.wel_header {
    z-index: 99;
     transition: transform .25s ease-in-out;

    &.sticky-top {
        
    }
    &.move-up {
        transform: translate3d(0, -100%, 0);
    }
    &.nav-shadow-show {
        box-shadow: 0 2px 2px 2px rgba(0, 0, 0, 0.14);
    }

    .wel-navbar {
        z-index: 99;
        padding: .5rem .75rem;

        .main-logo {
            padding: 0.25rem 0;
        }

        .navbar-toggle {
            padding: 0.625rem 0.625rem;
            margin-top: 0.5rem;
            margin-right: 1.5rem;
            margin-bottom: 0.5rem;
            background-color: transparent;
            background-image: none;
            border: 1px solid transparent;
            border-radius: 4px;

            @media all and (max-width: $screen-xs-min) {
                margin-right: 0;
            }

            @media all and (min-width: $screen-md-min) {
                display: none;
            }

            .icon-bar {
                display: block;
                width: 32px;
                height: 2px;
                border-radius: 1px;
                background-color: $darkgrey;

                + .icon-bar {
                    margin-top: 6px;
                }
            }

            &:focus, &:hover {
                background-color: transparent;
                outline: 0;
            }

            span {
                &.icon-bar {
                    transition: all 0.15s ease-in-out;
                }

                &:nth-child(1) {
                    transform: rotate(45deg);
                    transform-origin: 15% 10%;
                }

                &:nth-child(2) {
                    opacity: 0;
                }

                &:nth-child(3) {
                    transform: rotate(-45deg);
                    transform-origin: 10% 90%;
                }
            }

            &.collapsed span {
                &:nth-child(1),
                &:nth-child(3) {
                    transform: rotate(0);
                }

                &:nth-child(2) {
                    opacity: 1;
                }
            }

            &:focus, &:hover {
                background-color: transparent;
            }
        }

        .navbar-nav {
            z-index: 9;

            @media only screen and (max-width: $screen-md-min) {
                max-width: 720px;
                margin: auto;
            }

            @media only screen and (max-width: $screen-sm-min) {
                max-width: 540px;
            }

            .nav-item {
                margin: 0 1rem;
                position: relative;
                text-align: left;

                @media only screen and (max-width: $screen-md-min) {
                    margin: 0 1rem;
                    padding: .25rem 0;
                }

                @media only screen and (max-width: $screen-sm-min) {
                    padding: .25rem 1rem;
                }

                @media only screen and (max-width: $screen-xs-min) {}

                &:not(:last-child) {
                    @media only screen and (max-width: $screen-md-min) {
                        border-bottom: 1px solid rgba(0, 0, 0, 0.06);
                    }
                }

                &:hover {
                    .dropdown-menu {
                        top: 2.625rem;
                        opacity: 1;
                        visibility: visible;
                        transform: rotateX(0deg);
                        transition: .25s all ease-in-out;
                    }
                }

                &:nth-of-type(1) {
                    &.show,
                    &:hover {
                        .nav-link {
                            color: $success;
                        }
                    }

                    &.active {
                        .nav-link.highlight {
                            color: darken($success, 30%);
                            font-weight: 500;

                            &:before {
                                background-color: rgba($success, 0.82);
                                transform: rotate(0deg);
                            }
                        }
                    }

                    .nav-link {
                        &.highlight:before {
                            background-color: rgba($success, 0.36);
                        }
                    }
                }

                &:nth-of-type(2) {
                    // &.show,
                    &:hover {
                        .nav-link {
                            color: $primary;
                        }
                    }

                    &.active {
                        .nav-link.highlight {
                            color: darken($primary, 30%);
                            font-weight: 500;

                            &:before {
                                background-color: rgba($primary, 0.82);
                                transform: rotate(0deg);
                            }
                        }
                    }

                    .nav-link {
                        &.highlight:before {
                            background-color: rgba($primary, 0.36);
                        }
                    }
                }

                &:nth-of-type(3) {
                    &.show,
                    &:hover {
                        .nav-link {
                            color: $danger;
                        }
                    }

                    &.active {
                        .nav-link.highlight {
                            color: darken($danger, 30%);
                            font-weight: 500;

                            &:before {
                                background-color: rgba($danger, 0.82);
                                transform: rotate(0deg);
                            }
                        }
                    }

                    .nav-link {
                        &.highlight:before {
                            background-color: rgba($danger, 0.36);
                        }
                    }
                }

                &:nth-of-type(4) {
                    &.show,
                    &:hover {
                        .nav-link {
                            color: $warning;
                        }
                    }

                    &.active {
                        .nav-link.highlight {
                            color: darken($warning, 30%);
                            font-weight: 500;

                            &:before {
                                background-color: rgba($warning, 0.82);
                                transform: rotate(0deg);
                            }
                        }
                    }

                    .nav-link {
                        &.highlight:before {
                            background-color: rgba($warning, 0.36);
                        }
                    }
                }

                &.active {
                    .nav-link {
                        &.highlight {
                            @media all and (min-width: $screen-md-min) {
                                &:before {
                                    content: '';
                                    border-radius: 2px;
                                    width: 100%;
                                    height: .625rem;
                                    position: absolute;
                                    bottom: 0.75rem;
                                    left: 0;
                                    will-change: width;
                                    transition: width 0.125s ease-out;
                                    transform: rotate(-2deg);
                                    transform-origin: left bottom;
                                    z-index: -1;
                                }
                            }
                        }
                    }
                }

                &.dropdown {
                    .fa-chevron-down {
                        @media all and (max-width: $screen-md-min) {
                            transition: $transition-base;
                        }
                    }

                    &.show {
                        .fa-chevron-down {
                            @media all and (max-width: $screen-md-min) {
                                transform: rotate(180deg);
                                transition: $transition-base;
                            }
                        }
                    }
                }

                .nav-link {
                    color: $dark;
                    font-size: 1rem;
                    padding: 1rem .5rem;
                    user-select: none;

                    @media all and (max-width: $screen-md-min) {
                        padding: .75rem 0;
                    }

                    @media all and (max-width: $screen-sm-min) {
                        font-size: 1.125rem;
                    }

                    @media all and (max-width: $screen-xs-min) {}

                    @media all and (min-width: $screen-md-min) {
                        &:hover ~ .dropdown-menu {
                            display: block;
                            border-top: 3px solid $primary;
                            box-shadow: 0 2px 8px rgba($dark, 0.16);
                            margin: 1rem 0;

                            // padding: .5rem 0;
                            max-height: 800px;
                            visibility: visible;

                            &.fade-up {
                                transform: translateY(0rem);
                            }
                        }
                    }
                }

                &:not(.active) {
                    .highlight {
                        @media all and (min-width: $screen-md-min) {
                            &:hover {
                                &:before {
                                    width: 0;
                                }
                            }

                            &:before {
                                content: '';
                                border-radius: 2px;
                                width: 100%;
                                height: .625rem;
                                position: absolute;
                                bottom: 0.75rem;
                                left: 0;
                                will-change: width;
                                transition: width 0.125s ease-out;
                                transform: rotate(-2deg);
                                transform-origin: left bottom;
                                z-index: -1;
                            }
                        }
                    }
                }

                .dropdown-menu {
                    display: block;
                    background-color: #f8f9f9;
                    border: none;

                    // margin: 0;
                    // padding: 0;
                    max-height: 0;
                    visibility: hidden;
                    overflow: hidden;
                    transition: max-height .0625s ease-in-out;

                    @media all and (min-width: $screen-md-min) {
                        &:hover {
                            display: block;
                            border-top: 3px solid $primary;
                            box-shadow: 0 2px 8px rgba($dark, 0.16);
                            margin: 1rem 0;
                            padding: .5rem 0;
                            max-height: 800px;
                            visibility: visible;

                            &.fade-up {
                                transform: translateY(0rem);
                            }
                        }

                        &.fade-up {
                            transform: translateY(3rem);
                        }
                    }

                    &.show {
                        @media all and (max-width: $screen-md-min) {
                            border-top: 2px solid lighten($primary, 10%);
                            box-shadow: 0 1px 2px rgba($dark, 0.16);
                            margin: .25rem 0 1rem;
                            padding: .5rem 0;
                            max-height: 800px;
                            visibility: visible;
                        }
                    }

                    @media all and (max-width: $screen-md-min) {
                        display: block;
                        visibility: hidden;
                        margin: 0;
                        padding: 0;
                        min-width: 120px;
                        max-height: 0;

                        // opacity: 0;
                        visibility: hidden;
                        overflow: hidden;
                        transition: max-height .125s ease-in-out;
                    }

                    li {
                        flex: 1 1 auto;
                    }

                    .dropdown-header {
                        font-size: .75rem;
                        color: $darkgrey;

                        @media all and (max-width: $screen-md-min) {
                            font-size: .875rem;
                        }

                        @media all and (max-width: $screen-sm-min) {
                            font-size: 1rem;
                        }
                    }

                    .dropdown-item {
                        font-size: .875rem;
                        color: $dark;
                        margin-left: .5rem;

                        @media all and (max-width: $screen-md-min) {
                            font-size: 1rem;
                            padding: .375rem 1.5rem .375rem 2.25rem;
                            margin-left: 0;
                        }

                        @media all and (max-width: $screen-sm-min) {
                            font-size: 1.0625rem;
                            padding: .5rem 1.75rem .5rem 2.5rem;
                            margin-left: 0;
                        }

                        @media all and (max-width: $screen-xs-min) {
                            font-size: 1rem;
                            padding: .375rem 1.75rem .375rem 2.5rem;

                            // margin-left: 0;
                        }

                        &:focus,
                        &:hover {
                            background-color: rgba($primary, 0.12);
                            color: $primary;
                            padding-left: 1.75rem;
                        }
                    }
                }
            }
        }

        .navbar-collapse {
            @media only screen and (max-width: 991px) {
                position: absolute;
                top: 78px;
                left: 0;
                width: 100%;
                background-color: rgba(255, 255, 255, 1);
                border-top: lighten($w-primary, 7.5%) 3px solid;
                border-bottom: 1px solid #e5e5e5;
                box-shadow: 0 6px 12px rgba(lighten($w-primary, 5%), 0.08);
            }
        }
    }
}