/*  Pagination
----------------------------------------  */
.wel-pagination {
  background-color: #fff;
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  margin-top: 2rem;
  padding: 1rem 2rem;
  text-align: center;
}

.wel-pagination ul {
  font-size: 0;
  text-align: center;
  width: 100%;
}

.wel-pagination ul li {
  display: inline-block;
}

.wel-pagination ul li:first-child {
  float: left;
}

.wel-pagination ul li:last-child {
  float: right;
}

.wel-pagination ul li span {
  vertical-align: middle;
  font-size: .875rem;
}

.wel-pagination ul li a {
  display: block;
  color: #838588;
  font-size: 1rem;
  width: 100%;
  height: 100%;
  padding: .5rem;
}

.wel-pagination ul li a:hover {
  color: #00BCD1;
}

.wel-pagination ul li a.active {
  color: #212529;
}

.wel-pagination .arrow {
  position: relative;
  display: inline-block;
  background-color: #838588;
  width: 2rem;
  height: 2px;
  transition: all .125s ease-in-out;
  vertical-align: middle;
}

.wel-pagination .arrow.lt-long-arrow:before, .wel-pagination .arrow.rt-long-arrow:before {
  position: absolute;
  top: -4px;
  content: '';
  border-width: 5px;
  border-style: dashed;
  border-color: transparent;
}

.wel-pagination .arrow.lt-long-arrow:before {
  left: -6px;
  border-right-style: solid;
  border-right-color: #838588;
}

.wel-pagination .arrow.rt-long-arrow:before {
  right: -6px;
  border-left-style: solid;
  border-left-color: #838588;
}

.arrow-icon:hover {
  text-decoration: none;
}

.arrow-icon:hover .arrow {
  background-color: #00BCD1;
}

.arrow-icon:hover .arrow.lt-long-arrow:before {
  border-right-color: #00BCD1;
}

.arrow-icon:hover .arrow.rt-long-arrow:before {
  border-left-color: #00BCD1;
}
