@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Layout > Footer
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

.footer {
    // background-color: desaturate($w-dark,30%);
    background-color: #314a5d;
    color: $white;
    padding: 2.5rem 0 1.5rem;
    font-size: 1rem;
    text-align: center;

    // @media only screen and (max-width: $screen-xs-min) {
    //     padding: 1.5rem 0;
    // }


    p {
        margin: 0;
        padding-bottom: 1rem;
    }
    a {
        color: lighten($w-primary,15%);
        text-decoration: none;

        &:hover {
            color: lighten($w-primary,10%);
            text-decoration: underline;
        }
    }


}

