@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Pages > FAQ
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

/*  FAQ tabs
----------------------------------------  */
.faq-tabs {

    // @include gradient-x($primary ,$secondary);

    .tab-nav {
        display: inline-block;
        vertical-align: bottom;
        background-color: $smoke;
        border-radius: 6px;
        color: $dark;
        cursor: pointer;
        font-size: 1rem;
        margin-bottom: 1rem;
        padding: .75rem 1.25rem;
        transition: all .125s ease-in-out;

        &:not(:last-of-type) {
            margin-right: .75rem;
        }
    }

    > input {
        &:checked + .tab-nav {
            background-color: desaturate($w-lblue, 30%);
            color: $white;
            font-size: 1.25rem;
        }
    }

    label {
        transition: all .125s ease-in-out;

        &:hover {
            background-color: darken($smoke, 2%);
            color: darken($w-lblue, 10%);
        }
    }

    .faq-tabs-content {
        display: none;
        animation: fadeIn .75s ease-in-out;
    }
}

#faq1:checked ~ #faq1-content,
#faq2:checked ~ #faq2-content,
#faq3:checked ~ #faq3-content,
#faq4:checked ~ #faq4-content,
#faq5:checked ~ #faq5-content,
#faq6:checked ~ #faq6-content {
    display: block;
}

/*  FAQ Accordion
----------------------------------------  */
.accordion--item {
    position: relative;
    background-color: $light;
    border: 1px solid transparent;
    border-radius: 8px;
    box-shadow: 0 2px 4px -3px rgba($dark, 0.08);
    margin-top: 1rem;
    transition: all .125s ease-in-out;

    &:not(:last-of-type) {
        margin-bottom: 1.5rem;
    }

    &:hover {
        background-color: lighten(#e0f0f9, 3%);
        box-shadow: 0 6px 10px -6px rgba($dark, 0.1);

        dt {
            > strong {
                color: desaturate($w-lblue, 30%);
            }

            > font {
                color: desaturate($w-lblue, 30%);
            }

            > i {
                color: desaturate($w-lblue, 30%);
            }
        }
    }

    &.open {
        background-color: lighten(#e0f0f9, 4%);
        border: 1px solid #edf6fb;
        box-shadow: 0 6px 10px -6px rgba($dark, 0.1);

        dt {
            border-bottom: desaturate($w-lblue, 40%) 3px solid;

            > strong {
                color: desaturate($w-lblue, 30%);
            }

            > font {
                color: desaturate($w-lblue, 30%);
            }

            > i {
                color: desaturate($w-lblue, 30%);
            }
        }
    }

    dt {
        position: relative;
        border-radius: 8px 8px 0 0;
        cursor: pointer;
        padding: 1rem 1.5rem;
        user-select: none;

        * {
            transition: all .125s ease-in-out;
        }

        > strong {
            font-size: 2rem;
            color: desaturate($w-lblue, 20%);
            margin-right: .5rem;
        }

        > font {
            color: $dark;
            font-size: 1.125rem;
            font-weight: normal;
        }

        > i {
            font-size: 1.5rem;
            position: absolute;
            right: 1rem;
            top: 50%;
            transform: translateY(-50%);
        }
    }

    dd {
        position: relative;
        display: none;
        border-radius: 0 0 8px 8px;
        margin: 0;
        padding: 2rem;
        // background-color: lighten(#e0f0f9, 5%);
        background-color: $white;

        > p {
            margin-bottom: .25rem;
            font-size: 1rem;
            color: $dark;
        }

        a {
            font-size: 1rem;
            text-decoration: underline;
            color: $link;

            &:hover {
                text-decoration: none;
                color: $link-hover;
            }
        }
    }
}