@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Components > Modal
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

.modal {
    background: rgba($dark,0.36);



    .modal-header {
    	padding: 0.625rem 0.75rem 0.625rem 1.5rem;
    	
    	span {
    		color: $white;
    		font-size: 1.125rem;
    	}
    }


    .bg--primary {
    	background-color: darken($w-primary,5%);
    }

    .modal-body {
	    padding: 1rem 0;
	}
}


