@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Pages > Base
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

/*  Top Nav 頂端 Bar
----------------------------------------  */
.top-bar {
    background-color: $w-primary;
    color: $w-white;

    .header-link {
        float: right;
        margin: 0 .5rem 0 0;
        padding: 0;

        @media (max-width: $screen-xs-min) {
            float: none;
            margin: 0 auto;
            width: 100%;
        }

        li {
            float: left;
            padding: 0;
            margin: .375rem .5rem;
            list-style: none;

            @media (max-width: $screen-xs-min) {
                float: left;
                margin: .5rem .25rem;
                padding: 0;
                list-style: none;
            }
        }

        i {
            margin-right: .25rem;
        }

        a {
            background-color: $w-white;
            border: 1px solid $w-white;
            border-radius: 4px;
            color: $w-primary;
            font-size: .875rem;
            padding: .25rem .375rem;

            @media (max-width: $screen-xs-min) {
                display: block;
                padding: 4px 10px;
            }

            &:hover {
                opacity: 0.6;
                text-decoration: none;
            }
        }
    }
}

/*  Contain Bg 頁面內容區
----------------------------------------  */
.contain-bg {
    background-color: $default;
    padding: 1rem 0 2rem;
    @media only screen and (max-width: $screen-xs-min) {
        padding: 1rem 0;
    }

    .main-board {
        background-color: $white;
        box-shadow: 0 0 16px rgba(0, 0, 0, 0.1);
        border-radius: 4px;
        border-top: 4px solid $w-primary;
        margin: 2.75rem 1rem;
        padding: 2rem;

        @include clearfix;

        @media only screen and (max-width: $screen-sm-min) {
            margin: 2.25rem 1rem;
        }
    }

    .contact-box {
        border-style: solid;
        border-width: 1px;
        border-color: #e5e5e5;
        border-radius: 4px;
        margin-bottom: .625rem;
        padding: .75rem 1.5rem;
        overflow: visible;

        ul {
            list-style: inside;

            li {
                color: $grey;
                line-height: 2;
            }
        }
    }
}

/*  Step flow
----------------------------------------  */
ul.flow-group {
    position: relative;
    font-size: 0;
    margin: 3rem 1rem 0;

    @media only screen and (min-width: 576px) and (max-width: 768px) {}

    @media only screen and (max-width: 576px) {
        margin-left: 0;
        margin-right: 0;
    }

    &:before {
        content: '';
        width: 100%;
        height: .5rem;
        background-color: $grey-100;
        border-radius: 4px;
        position: absolute;
        left: 0;
        top: 32.5%;
        transform: translateY(-50%);
    }

    .undo {
        .flow-number {
            background: rgb(127, 208, 255);
            background: -moz-linear-gradient(135deg, rgba(127, 208, 255, 1) 50%, rgba(127, 194, 239, 1) 55%);
            background: -webkit-linear-gradient(135deg, rgba(127, 208, 255, 1) 50%, rgba(127, 194, 239, 1) 55%);
            background: linear-gradient(135deg, rgba(127, 208, 255, 1) 50%, rgba(127, 194, 239, 1) 55%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#7fd0ff",endColorstr="#7fc2ef",GradientType=1);
        }

        span {
            color: $w-lightfrey;
        }
    }

    li {
        display: inline-block;
        vertical-align: middle;
        width: (100% / 6);
        text-align: center;
        position: relative;
        z-index: 10;

        &:not(.undo) {
            &:before {
                background: -webkit-repeating-linear-gradient(45deg, #1ea4ec, #1ea4ec 4%, #1f8bc5 4%, #1f8bc5 10px);
                background: repeating-linear-gradient(45deg, #1ea4ec, #1ea4ec 4%, #1f8bc5 4%, #1f8bc5 8%);
            }

            &:nth-child(1):before {
                animation: animate-flow .75s 0s backwards linear;
            }

            &:nth-child(2):before {
                animation: animate-flow .725s .75s backwards linear;
            }

            &:nth-child(3):before {
                animation: animate-flow .7s 1.475s backwards linear;
            }

            &:nth-child(4):before {
                animation: animate-flow .675s 2.15s backwards linear;
            }

            &:nth-child(5):before {
                animation: animate-flow .65s 2.8s backwards linear;
            }

            &:nth-child(6):before {
                animation: animate-flow .625s 3.425s backwards linear;
            }
        }

        &:first-child:before {
            border-radius: 4px 0 0 4px;
        }

        &:last-child:before {
            border-radius: 0 4px 4px 0;
        }

        &:before {
            content: '';
            width: 100%;
            height: .5rem;
            background-color: $grey-100;
            position: absolute;
            left: 0;
            top: 32.5%;
            transform: translateY(-50%);
            z-index: -1;
        }

        .flow-number {
            background: rgb(66, 162, 255);
            background: -moz-linear-gradient(135deg, rgba(66, 162, 255, 1) 50%, rgba(55, 132, 223, 1) 55%);
            background: -webkit-linear-gradient(135deg, rgba(66, 162, 255, 1) 50%, rgba(55, 132, 223, 1) 55%);
            background: linear-gradient(135deg, rgba(66, 162, 255, 1) 50%, rgba(55, 132, 223, 1) 55%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#42a2ff",endColorstr="#3784df",GradientType=1);
            border-radius: 35px;
            color: $white;
            font-size: 2rem;
            width: 3.75rem;
            height: 3.75rem;
            margin: 0 auto 8px auto;
            padding-top: .375rem;
            text-align: center;

            @media only screen and (min-width: 576px) and (max-width: 768px) {
                font-size: 1.5rem;
                width: 3rem;
                height: 3rem;
            }

            @media only screen and (max-width: 576px) {
                font-size: 1rem;
                width: 2rem;
                height: 2rem;
            }
        }

        span {
            color: $w-space;
            font-size: 1rem;
            font-weight: bold;
            white-space: pre;

            @media only screen and (max-width: 576px) {
                font-size: .75rem;
            }
        }
    }
}

@keyframes animate-flow {
    0% {
        width: 0;
    }

    100% {
        width: 100%;
    }
}

/*  Apply Leftbox
----------------------------------------  */

.userA-title {
