/*  Progress Bar
----------------------------------------  */
.progress-area {
    position: relative;
    margin: 3.5rem auto 6rem;
    padding: 4rem 0 0;
    text-align: center;

    @media only screen and (max-width: 576px) {
        width: 90%;
    }

    .progress {
        background-color: #E0E0E0;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
        border: 6px solid #fff;
        height: 3.125rem;
        border-radius: 2rem;
        overflow: visible;

        .progress-bar {
            border-radius: 2rem;
            overflow: visible;
            padding-right: .375rem;
            position: relative;
            text-align: right;
            animation: animate-positive 1s;

            .progress-btn {
                position: absolute;
                right: -2rem;
                top: 57.5%;
                transform: translateY(-50%);
            }

            .progress-doctor {
                position: absolute;
                right: 2.5rem;
                top: -1rem;
            }
        }

        .progress-bg {
            background: rgb(30, 214, 255);
            background: -moz-linear-gradient(180deg, rgba(30, 214, 255, 1) 0%, rgba(32, 210, 252, 1) 64%, rgba(20, 198, 239, 1) 70%, rgba(32, 210, 252, 1) 100%);
            background: -webkit-linear-gradient(180deg, rgba(30, 214, 255, 1) 0%, rgba(32, 210, 252, 1) 64%, rgba(20, 198, 239, 1) 70%, rgba(32, 210, 252, 1) 100%);
            background: linear-gradient(180deg, rgba(30, 214, 255, 1) 0%, rgba(32, 210, 252, 1) 64%, rgba(20, 198, 239, 1) 70%, rgba(32, 210, 252, 1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#1ed6ff",endColorstr="#20d2fc",GradientType=1);
        }
    }

    .progress-note {
        background-color: #31495d;
        border-radius: 1.25rem;
        font-weight: bold;
        padding: 1rem 1.5rem;
        color: #fff;
        position: absolute;
        animation: animate-lf 1.5s;

        &:before {
            content: '';
            background: url("../../images/bubble_bt.svg") no-repeat center;
            background-size: contain;
            width: 2.75rem;
            height: 1rem;
            left: 25%;
            bottom: -.875rem;
            position: absolute;
        }

        p {
            // position: relative;
            // // top: -0.375rem;
       
        }

        span {
            font-size: 0.75rem;
        }
    }

    &.w--10 {
        .progress-bar {
            width: 10%;
        }

        .progress-note {
            left: 0;
            top: -2rem;
        }
    }

    &.w--20 {
        .progress-bar {
            width: 20%;
        }

        .progress-note {
            left: 10%;
            top: -2rem;
        }
    }

    &.w--30 {
        .progress-bar {
            width: 30%;
        }

        .progress-note {
            left: 20%;
            top: -2rem;
        }
    }

    &.w--40 {
        .progress-bar {
            width: 40%;
        }

        .progress-note {
            left: 30%;
            top: -2rem;
        }
    }

    &.w--50 {
        .progress-bar {
            width: 50%;
        }

        .progress-note {
            left: 40%;
            top: -2rem;
        }
    }

    &.w--60 {
        .progress-bar {
            width: 60%;
        }

        .progress-note {
            left: 50%;
            top: -2rem;
        }
    }

    &.w--70 {
        .progress-bar {
            width: 70%;
        }

        .progress-note {
            left: 60%;
            top: -2rem;
        }
    }

    &.w--80 {
        .progress-bar {
            width: 80%;
        }

        .progress-note {
            left: 70%;
            top: -2rem;
        }
    }

    &.w--90 {
        .progress-bar {
            width: 90%;
        }

        .progress-note {
            left: 80%;
            top: -2rem;
        }
    }

    &.w--100 {
        .progress-bar {
            width: 100%;
        }

        .progress-note {
            left: 20%;
            top: -2.75rem;

            &:before {
                right: 2%;
                left: auto;
            }
        }
    }
}

@keyframes animate-positive {
    0% {
        width: 0;
    }
}

@keyframes animate-lf {
    0% {
        left: -100px;
    }

    1000% {
        left: 0;
    }
}

.text-alert {
    color: #c7000e;
}

