@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Components > iConFonts
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */
@font-face {
    font-family: 'welfare';
    src: url("fonts/welfare.eot?8xfyhg");
    src: url("fonts/welfare.eot?8xfyhg#iefix") format("embedded-opentype"), url("fonts/welfare.ttf?8xfyhg") format("truetype"), url("fonts/welfare.woff?8xfyhg") format("woff"), url("fonts/welfare.svg?8xfyhg#welfare") format("svg");
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="welfare-"], [class*=" welfare-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'welfare' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.welfare-health_Personnel_h span {
    position: relative;

    &:nth-child(1):before {
        content: "\e900";
        color: $dark;
    }

    &:nth-child(2):before {
        content: "\e901";
        position: absolute;
        left: -1em;
        color: $primary;
    }
}

.welfare-health_Personnel:before {
    content: "\e902";
}

.welfare-health_Institute_h span {
    position: relative;

    &:nth-child(1):before {
        content: "\e903";
        color: $dark;
    }

    &:nth-child(2):before {
        content: "\e904";
        position: absolute;
        left: -1em;
        color: $secondary;
    }
}

.welfare-health_Institute:before {
    content: "\e905";
}