@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Base > Reset
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

html {
    font-size: $font-size-html-base;
    scroll-behavior: smooth;

    @media (max-width: $screen-md-min) {
        font-size: $font-size-html-md;
    }

    @media (max-width: $screen-sm-min) {
        font-size: $font-size-html-sm;
    }

    @media (max-width: $screen-xs-min) {
        font-size: $font-size-html-xs;
    }
}

body {
    line-height: 1.5;
    margin: 0;
    min-height: 100%;
    text-rendering: optimizeSpeed;
}

small {
    font-size: 75%;
}

big {
    font-size: 125%;
}

a {
    transition: all .125s ease-in-out;
}

a:hover {
    color: #84caf7;
}

a[href^="http"]:empty:before {
    content: attr(href);
}

figure,
h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

b,
strong,
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: bolder;
}

dl,
ol,
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

table {
    width: 100%;
    background-color: transparent;
    border-collapse: collapse;
}

button,
input {
    overflow: visible;
}

button,
select {
    text-transform: none;
}

button,
input,
optgroup,
select,
textarea {
    font-size: 100%;
    line-height: 1.5;
    margin: 0;
    outline: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
    -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
}

fieldset {
    padding: 0.35em 0.75em 0.625em;
}

legend {
    color: inherit;
    display: table;
    max-width: 100%;
    padding: 0;
    white-space: normal;
}

progress {
    vertical-align: baseline;
}

textarea {
    overflow: auto;
}

img,
svg,
image {
    border-style: none;
    height: auto;
    max-width: 100%;
    vertical-align: middle;
}

[type="checkbox"],
[type="radio"] {
    padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
    height: auto;
}

[type="search"] {
    -webkit-appearance: textfield;
    outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit;
}

details {
    display: block;
}

summary {
    display: list-item;
}

template {
    display: none;
}

[hidden] {
    display: none;
}

em {
    font-style: italic;
}