@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Components > Loading Animation
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

#loadingMask {
    display: none;
    text-align: center;
    padding: 1rem 3rem;
}

.ani-lightSpeedIn {
    animation: lightSpeedIn 1.5s infinite both linear;
}

@keyframes lightSpeedIn {
    0% {
        transform: translateX(35%) skewX(-5deg);
        opacity: 1;
    }

    20% {
        transform: translateX(17.5%) skewX(-2.5deg);
    }

    60% {
        transform: translateX(-17.5%) skewX(0deg);
        opacity: 0.88;
    }

    100% {
        transform: translateX(-35%) skewX(0deg);
    }
}

.ani-ambulance {
    width: 8.5rem;
    height: 4.5rem;
}

.load-txt {
    padding-top: .75rem;

    span {
        display: inline-block;
        font-weight: bolder;
        animation: flip 2.6s infinite linear;

        &:nth-child(2) {
            animation-delay: 0.3s;
        }

        &:nth-child(3) {
            animation-delay: 0.6s;
        }

        &:nth-child(4) {
            animation-delay: 0.9s;
        }

        &:nth-child(5) {
            animation-delay: 1.2s;
        }

        &:nth-child(6) {
            animation-delay: 1.5s;
        }
    }
}

@keyframes flip {
    35% {
        transform: rotateX(360deg);
    }

    100% {
        transform: rotatex(360deg);
    }
}

