@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Components > Heading
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */
.Wel_apply {
    .wel-heading {
        @media (max-width: $screen-xs-min) {
            margin-top: 2rem;
        }
    }
}

.wel-heading {
    position: relative;
    margin-bottom: .5rem;
    letter-spacing: 2px;
    text-align: center;

    h3 {
        font-size: 1.5rem;
    }

    &.wel-heading--sm {
        h3 {
            font-size: 1.25rem;
        }
    }

    &.wel-heading--dark {
        h3 {
            position: relative;
            display: inline-block;

            &:after,
            &:before {
                content: '';
                background-image: url("../../images/heading_line.svg");
                background-repeat: no-repeat;
                opacity: .44;
                transform: translateY(-50%);
                height: pxTorem(14px);
                width: pxTorem(48px);
            }

            &:after {
                @include position(absolute, $L: -4rem, $T: 50%);
            }

            &:before {
                @include position(absolute, $R: -4rem, $T: 50%);
            }
        }

        big {
            color: $w-blue;
        }
    }

    &.wel-heading--white {
        h3 {
            position: relative;
            display: inline-block;
            color: $w-white;

            &:after,
            &:before {
                content: '';
                background-image: url("../../images/heading_line_w.svg");
                background-repeat: no-repeat;
                opacity: .44;
                transform: translateY(-50%);
                height: pxTorem(14px);
                width: pxTorem(48px);
            }

            &:after {
                @include position(absolute, $L: -4rem, $T: 50%);
            }

            &:before {
                @include position(absolute, $R: -4rem, $T: 50%);
            }
        }

        big {
            color: $w-blue;
        }
    }

    // .btn-more a {
    //     float: right;
    //     border: $lightgrey 1px solid;
    //     border-radius: 4px;
    //     color: $darkgrey;
    //     font-size: .9375rem;
    //     font-weight: normal;
    //     padding: 0.375rem 1.125rem;
    //     display: inline-block;
    //     letter-spacing: normal;

    //     &:hover {
    //         background-color: $primary;
    //         border: transparent 1px solid;
    //         color: $white;
    //         text-decoration: none;
    //     }
    // }
}

/*  Page Title 頁面小標題
----------------------------------------  */
.page-title {
    position: relative;
    border-radius: 2px;
    font-size: 1.5rem;
    padding: .3125rem 2.75rem;
    margin: 3.25rem 0 0;
    letter-spacing: 2px;

    @media only screen and (max-width: $screen-xs-min) {
        font-size: 1.375rem;
        padding: .3125rem 1.8125rem;
        margin: 2.5rem 0 0 1rem;
    }

    span {
        position: relative;
        line-height: 1.75;

        font {
            color: darken($w-primary, 15%);
        }

        &:before {
            content: '';
            width: .5rem;
            height: .5rem;
            position: absolute;
            top: 57.5%;
            left: -1.25rem;
            transform: translateY(-50%);
            border-radius: 0 4px 4px 4px;
            background: rgba($w-primary, 0.48);
        }

        &:after {
            content: '';
            width: 100%;
            height: .5rem;
            border-radius: 2px;
            position: absolute;
            bottom: -0.25rem;
            left: 0; // transform: rotate(-2deg);
            background-image: lighten($w-primary, 20%);
            background-image: -moz-linear-gradient(90deg, rgba(lighten($w-primary, 20%), 0.66) 0%, rgba(lighten($w-primary, 25%), 0.48) 100%);
            background-image: -webkit-linear-gradient(90deg, rgba(lighten($w-primary, 20%), 0.66) 0%, rgba(lighten($w-primary, 25%), 0.48) 100%);
            background-image: linear-gradient(90deg, rgba(lighten($w-primary, 20%), 0.66) 0%, rgba(lighten($w-primary, 25%), 0.48) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#38ebff",endColorstr="#38ebff",GradientType=1);
            background-size: 100% .75rem;
            background-repeat: no-repeat;
            background-position-x: 0;
            background-position-y: 100%;
        }

    }

    &.secondary {
        span {
            font {
                color: darken($w-secondary, 15%);
            }


            &:before {
                background: rgba($w-secondary, 0.66);
            }

            &:after {
                background: lighten($w-secondary, 10%);
                background: -moz-linear-gradient(90deg, rgba(lighten($w-secondary, 10%), 1) 0%, rgba(lighten($w-secondary, 15%), 0.66) 100%);
                background: -webkit-linear-gradient(90deg, rgba(lighten($w-secondary, 10%), 1) 0%, rgba(lighten($w-secondary, 15%), 0.66) 100%);
                background: linear-gradient(90deg, rgba(lighten($w-secondary, 10%), 1) 0%, rgba(lighten($w-secondary, 15%), 0.66) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#7dd1ef",endColorstr="#7dd1ef",GradientType=1);
                background-size: 100% .75rem;
                background-repeat: no-repeat;
                background-position-x: 0;
                background-position-y: 100%;
            }


        }
    }

    &.w-lblue {
        span {
            font {
                color: darken($w-lblue, 15%);
            }

            &:before {
                background: rgba($w-lblue, 0.66);
            }
            &:after {
                background: lighten($w-lblue, 20%);
                background: -moz-linear-gradient(90deg, rgba(lighten($w-lblue, 20%), 0.88) 0%, rgba(lighten($w-lblue, 20%), 0.66) 100%);
                background: -webkit-linear-gradient(90deg, rgba(lighten($w-lblue, 20%), 0.88) 0%, rgba(lighten($w-lblue, 20%), 0.66) 100%);
                background: linear-gradient(90deg, rgba(lighten($w-lblue, 20%), 0.88) 0%, rgba(lighten($w-lblue, 20%), 0.66) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#8c9ae7",endColorstr="#8c9ae7",GradientType=1);
                background-size: 100% .75rem;
                background-repeat: no-repeat;
                background-position-x: 0;
                background-position-y: 100%;
            }
        }
    }

    &.w-tertiary {
        span {
            font {
                color: darken($w-tertiary, 15%);
            }

            &:before {
                background: rgba($w-tertiary, 0.66);
            }
            &:after {
                background: lighten($w-tertiary, 20%);
                background: -moz-linear-gradient(90deg, rgba(lighten($w-tertiary, 20%), 0.88) 0%, rgba(lighten($w-tertiary, 20%), 0.66) 100%);
                background: -webkit-linear-gradient(90deg, rgba(lighten($w-tertiary, 20%), 0.88) 0%, rgba(lighten($w-tertiary, 20%), 0.66) 100%);
                background: linear-gradient(90deg, rgba(lighten($w-tertiary, 20%), 0.88) 0%, rgba(lighten($w-tertiary, 20%), 0.66) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ababdf",endColorstr="#ababdf",GradientType=1);
                background-size: 100% .75rem;
                background-repeat: no-repeat;
                background-position-x: 0;
                background-position-y: 100%;
            }
        }
    }
}

/*  Contact Title聯絡我們標題 
----------------------------------------  */
.contact-title {
    position: relative;
    border-radius: 2px;
    font-size: 1.375rem;
    padding: .3125rem .75rem;
    margin: 1.25rem .75rem .75rem;

    &:before {
        content: '';
        width: 12px;
        height: 12px;
        position: absolute;
        top: 50%;
        left: -1rem;
        transform: translateY(-50%) rotate(45deg);
        border-radius: 4px;
        background: rgba($w-primary, 0.36);
    }

    @media only screen and (max-width: $screen-xs-min) {
        font-size: 1.25rem;
        padding: .3125rem .625rem;
        margin: 1.25rem .3125rem .625rem 1rem;
    }

    span {
        color: $w_blue;
    }
}