@charset "utf-8";

/** –––––––––––––––––––––––––––––––––––––––––––––––––––––––––
//  Components > rtTools
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

.go-to-top {
    display: none;
    width: 4.5rem;
    background-color: white;
    border: $grey-100 1px solid;
    border-radius: 8px;
    box-shadow: 2px 2px 4px 0 rgba($dark, 0.18);
    color: $dark;
    display: block;
    font-size: .875rem;
    padding: .5rem .25rem .375rem;
    letter-spacing: 1px;

    @include opacity(0.66);

    transition: $transition-base;
    z-index: 99;

    &:hover {
        box-shadow: 2px 2px 8px 0 rgba($dark, 0.36);
    }

    &:hover,
    &:focus:hover {
        color: $w-primary;

        @include opacity(1);
    }

    &:focus {
        outline: none;

        @include opacity(1);
    }
}

.thruster {
    position: relative;
    left: 20%;
    top: 70%;
    transform: scale(0.75);
    z-index: -1;
    animation: 500ms thfadein .25s forwards;

    &.thruster-animation {
        padding-top: .875rem;
        margin-bottom: .5rem;
    }

    .flame-wrapper {
        position: relative;

        &.flame-animation {
            animation: flicker 400ms ease-in infinite;

            .flame {
                &_1 {
                    width: pxTorem(30px);
                    height: pxTorem(30px);
                    background: $danger;
                }

                &_2 {
                    left: pxTorem(5px);
                    width: pxTorem(20px);
                    height: pxTorem(20px);
                    background: $warning;
                }

                &_3 {
                    left: pxTorem(10px);
                    width: pxTorem(10px);
                    height: pxTorem(10px);
                    background: $w_orange;
                }

                &_4 {
                    left: pxTorem(12.5px);
                    width: pxTorem(5px);
                    height: pxTorem(5px);
                    background: lighten($warning, 20%);
                }
            }
        }

        .flame {
            bottom: 0;
            position: absolute;
            border-radius: 50% 50% 50% 0%;
            transform: rotate(-45deg);
            width: 0;
            height: 0;
        }
    }
}

.zoomIn {
    animation: rt-zoomIn 2s both;
}

@keyframes rt-zoomIn {
    0% {
        transform: scale(0.5, 0.5);
    }

    40% {
        transform: scale(1, 1);
    }
}

@keyframes thfadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 100;
    }
}

@keyframes flicker {
    0% {
        transform: rotate(-1deg);
    }

    20% {
        transform: rotate(2deg) scaleY(1.2);
    }

    40% {
        transform: rotate(-1deg);
    }

    60% {
        transform: rotate(1deg);
    }

    80% {
        transform: rotate(-1deg) scaleY(0.9);
    }

    100% {
        transform: rotate(1deg);
    }
}